import { useState } from "react";
import "./styles.css";

// JSX- babel motor jaavascript converte jsx em  texto que consegue ser lido pelo react
// melhor maneira de descrver uma estrutura em arvore

//elemento - representa menor parte de uma extrutura em react
const elemento = (
  <div>
    <h2>Ola Primeiro Projeto em React</h2>
  </div>
);

//componente ele retorna o elemento
// consegue quebrar a interface  em mais partes
// conseguindo assim criar elementos reutilizaveis

//componente e a uniao de varios elementos

//hook - fura {useState} ancora para fora da função
//como tirar uma foto para fora da função
// armazena uma variavel fora do escopo

export default function App() {
  // chute o<>300
  // quantos palpites a maquina deu
  // entrada, rodando, fim

  const [estado, setEstado] = useState("entrada");

  const iniciarJogo = () => {
    setEstado("RODANDO");
  };

  if (estado === "entrada") {
    return <button onClick={iniciarJogo}>Começar a Jogar</button>;
  }

  return (
    <div className="App">
      <button>menor</button>
      <button>acertou</button>
      <button>maior</button>
    </div>
  );
}

